    flex-direction: row;
    flex-wrap: wrap;
    min-height: 73px;
    padding: 0;
    border-left: 0;
    border-right: 0;
  }
}
.supplements__heading {
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  color: #222222;
  text-transform: uppercase;
  margin: 0;
  margin-top: 14px;
}
@media (min-width: 768px) {
  .supplements__heading {
    font-size: 20px;
    line-height: 20px;
    min-height: 10px;
    margin-top: 26px;
  }
}
@media (min-width: 1440px) {
  .supplements__heading {
    width: 244px;
    margin-top: 25px;
    margin-right: 40px;
  }
}
.supplements__info {
  margin: 0;
  margin-top: 12px;
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .supplements__info {
    min-width: 140px;
    justify-self: flex-end;
    min-height: 10px;
    margin: 0;
    margin-top: 30px;
  }
}
@media (min-width: 1440px) {
  .supplements__info {
    margin-top: 30px;
  }
}
.supplements__name {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 768px) {
  .supplements__name {
    font-size: 16px;
    line-height: 20px;
    margin-right: 50px;
  }
}
@media (min-width: 1440px) {
  .supplements__name {
    margin-right: 0;
  }
}
.supplements__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (min-width: 1440px) {
  .supplements__item {
    min-width: 278px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.supplements__description {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #444444;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .supplements__description {
    font-size: 16px;
    line-height: 20px;
  }
}
.supplements__button {
  min-width: 280px;
  min-height: 40px;
}
@media (min-width: 768px) {
  .supplements__button {
    min-width: 100%;
    min-height: 40px;
    margin-top: 0;
  }
}
@media (min-width: 1440px) {
  .supplements__button {
    min-width: 244px;
    margin-top: 15px;
  }
}
.special-offer {
  background-image: url("../img/special-offer-background.png");
  background-repeat: no-repeat;
  min-width: 280px;
  margin: 0 auto;
  background-position: center;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .special-offer {
    background-image: url("../img/special-offer-background@2x.png");
    background-position: top center;
    background-size: cover;
  }
}
@media (min-width: 768px) {
  .special-offer {
    background-image: url("../img/special-offer-background-tablet.png");
    min-width: 707px;
    min-height: 200px;
    background-size: cover;
    margin: 0;
    width: 100%;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .special-offer {
    background-image: url("../img/special-offer-background-tablet@2x.png");
    background-position: top center;
    background-size: cover;
  }
}
@media (min-width: 1440px) {
  .special-offer {
    background-image: url("../img/special-offer-background-desktop.png");
    width: 245px;
    min-width: 245px;
    min-height: 289px;
    margin: 80px 0 auto 0;
    background-size: cover;
    background-position: top center;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .special-offer {
    background-image: url("../img/special-offer-background-desktop@2x.png");
    background-position: top center;
    background-size: cover;
  }
}
.special-offer__wrapper {
  background-color: rgba(105, 183, 56, 0.85);
  min-width: 100%;
  min-height: 289px;
  background-size: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 37px;
  margin-top: 40px;
  position: relative;
}
@media (min-width: 768px) {
  .special-offer__wrapper {
    flex-direction: row;
    justify-content: flex-end;
    margin-top: 70px;
    margin-bottom: 75px;
    min-height: 200px;
    background-size: 100% 200px;
  }
}
@media (min-width: 1440px) {
  .special-offer__wrapper {
    width: 245px;
    min-width: 245px;
    min-height: 292px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto 0;
  }
}
.special-offer__wrapper::after {
  position: absolute;
  content: "";
  background: url("../img/icon_gift.svg");
  min-width: 78px;
  height: 80px;
  top: 60px;
}
@media (min-width: 768px) {
  .special-offer__wrapper::after {
    left: 80px;
  }
}
@media (min-width: 1440px) {
  .special-offer__wrapper::after {
    left: 84px;
    top: 57px;
  }
}
.special-offer__phrase {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #ffffff;
  width: 161px;
  min-height: 61px;
  text-align: center;
  margin-top: 160px;
}
@media (min-width: 768px) {
  .special-offer__phrase {
    margin: 0;
    padding: 0;
    margin-right: 125px;
    text-align: start;
  }
}
@media (min-width: 1440px) {
  .special-offer__phrase {
    text-align: center;
    margin: 0;
    margin-top: 142px;
  }
}
