
.advantages__wrapper {
  min-width: 280px;
  padding: 0 20px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    padding: 0 30px;
  }

  @media (min-width: @desktop-width) {
    width: 1220px;
    min-height: 280px;
    padding: 0 110px;
  }
}

.advantages__heading {
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  color: @basic-black;
  margin: 0;
  margin-bottom: 27px;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-top: 10px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 40px;
    margin-top: 70px;
  }
}

.advantages__list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 55px;
    margin-bottom: 20px;
  }

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 70px;
    margin: 0;
    min-height: 280px;
  }
}

.advantages__item {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: @special-dark-grey;
  padding-left: 80px;
  position: relative;
  min-height: 50px;


  &:not(:last-child) {
    padding-bottom: 25px;
  }

  &:first-child::before {
    position: absolute;
    content: "";
    background: url("../img/leaf-list.svg");
    background-repeat: no-repeat;
    width: 60px;
    height: 60px;
    top: -2px;
    left: 0;
    background-color: @basic-green;
    background-position: center;
  }

  &:nth-child(2)::before {
    position: absolute;
    content: "";
    background-image: url("../img/powder-list.svg");
    background-repeat: no-repeat;
    width: 60px;
    height: 60px;
    top: -2px;
    left: 0;
    background-color: @basic-green;
    background-position: center;
  }

  &:nth-child(3)::before {
    position: absolute;
    content: "";
    background-image: url("../img/meal-list.svg");
    background-repeat: no-repeat;
    width: 60px;
    height: 60px;
    top: -2px;
    left: 0;
    background-color: @basic-green;
    background-position: center;
  }

  &:last-child::before {
    position: absolute;
    content: "";
    background: url("../img/time-list.svg");
    background-repeat: no-repeat;
    width: 60px;
    height: 60px;
    top: -1px;
    left: 0;
    background-color: @basic-green;
    background-position: center;
  }

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
    padding: 145px 0 57px 0;
    box-sizing: border-box;

    &:first-child {
      width: 230px;
      min-height: 280px;
      background: url("../img/number-1.svg");
      background-repeat: no-repeat;
      background-position: top 30px right 35px;
    }

    &:first-child::before {
      width: 80px;
      height: 80px;
      top: 30px;
      left: 0;
      background-color: @basic-green;
      background-position: center;
    }

    &:nth-child(2) {
      width: 246px;
      min-height: 280px;
      background-image: url("../img/number-2.svg");
      background-repeat: no-repeat;
      background-position: top 30px right 10px;
    }

    &:nth-child(2)::before {
      width: 80px;
      height: 80px;
      top: 30px;
      left: 0;
      background-color: @basic-green;
      background-position: center;
      background-repeat: no-repeat;
    }

    &:nth-child(3) {
      width: 247px;
      min-height: 280px;
      background-image: url("../img/number-3.svg");
      background-repeat: no-repeat;
      background-position: top 10px right 10px;
      padding-top: 128px;
    }

    &:nth-child(3)::before {
      width: 80px;
      height: 80px;
      top: 15px;
      left: 0;
      background-color: @basic-green;
      background-position: center;
      background-repeat: no-repeat;
    }

    &:last-child {
      width: 249px;
      min-height: 280px;
      background-image: url("../img/number-4.svg");
      background-repeat: no-repeat;
      background-position: top 12px right 0;
      padding-top: 128px;
    }

    &:last-child::before {
      width: 80px;
      height: 80px;
      top: 15px;
      left: 0;
      background-color: @basic-green;
      background-position: center;
    }
  }

  @media (min-width: @desktop-width) {
    &:first-child {
      width: 230px;
    }

    &:nth-child(2) {
      width: 246px;
      background-position: top 30px right 15px;
      margin-left: 3px;
    }

    &:nth-child(3) {
      width: 248px;
      background-position: top 15px right 10px;
      margin-top: 15px;
      margin-left: 5px;
      margin-right: 0;
    }

    &:last-child {
      width: 245px;
      background-position: top 15px right 0;
      margin-top: 15px;
      margin-left: 5px;
    }
  }
}
